export default {
	data() {
		return {
			id: null,
			isHaveHuizi:false,
			title: '详情',
			uesr_huizi_data:{},
			tableData:[],
			delivered:0,
			earned_surplus:0,
		}
	},
	onLoad: function(option) {
		//获取具体用户ID，用于查询具体会子
		this.id = option.id;
		uni.getStorage({
		    key: this.id + '_key',
		    success:( res ) => {
		       this.isHaveHuizi = res.data.self_huzi.length == 0 ? false : true ;
			   this.uesr_huizi_data = {
				   name : res.data.name,
				   phone : res.data.phone,
			   }
			   this.tableData = res.data.self_huzi;
			   this.getDelivered( this.tableData );
		    }
		});
		
	},
	methods:{
		createHuizi(){
			uni.navigateTo({
				url: '/pages/user/createhuizi/index?id='+ this.id,
				animationType: 'pop-in',
				animationDuration: 200,
			});
		},
		getDelivered( data ){
			let len = data.length;
			let delivered = 0 ;
			let earned_surplus = 0;
			for( let i = 0 ;i <len ;i++){
				delivered += data[i].delivered;
				earned_surplus += data[i].earned_surplus;
			}
			this.delivered= delivered;
			this.earned_surplus= earned_surplus;
		}
	},
}
